import React, { useState, useRef, useEffect } from "react";
import { styled } from "@mui/material/styles";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import Slider from "@mui/material/Slider";
import IconButton from "@mui/material/IconButton";
import Stack from "@mui/material/Stack";
import PauseRounded from "@mui/icons-material/PauseRounded";
import PlayArrowRounded from "@mui/icons-material/PlayArrowRounded";
import FastForwardRounded from "@mui/icons-material/FastForwardRounded";
import FastRewindRounded from "@mui/icons-material/FastRewindRounded";
import FirstPage from "@mui/icons-material/FirstPage";
import LastPage from "@mui/icons-material/LastPage";
import VolumeUpRounded from "@mui/icons-material/VolumeUpRounded";
import VolumeDownRounded from "@mui/icons-material/VolumeDownRounded";

import FormLabel from "@mui/material/FormLabel";
import FormControl from "@mui/material/FormControl";
import FormGroup from "@mui/material/FormGroup";
import FormControlLabel from "@mui/material/FormControlLabel";
import Switch from "@mui/material/Switch";

const WallPaper = styled("div")({
  position: "absolute",
  width: "100%",
  height: "100%",
  top: 0,
  left: 0,
  overflow: "hidden",
  background: "linear-gradient(rgb(255, 38, 142) 0%, rgb(255, 105, 79) 100%)",
  transition: "all 500ms cubic-bezier(0.175, 0.885, 0.32, 1.275) 0s",
  "&::before": {
    content: '""',
    width: "140%",
    height: "140%",
    position: "absolute",
    top: "-40%",
    right: "-50%",
    background:
      "radial-gradient(at center center, rgb(62, 79, 249) 0%, rgba(62, 79, 249, 0) 64%)",
  },
  "&::after": {
    content: '""',
    width: "140%",
    height: "140%",
    position: "absolute",
    bottom: "-50%",
    left: "-30%",
    background:
      "radial-gradient(at center center, rgb(247, 237, 225) 0%, rgba(247, 237, 225, 0) 70%)",
    transform: "rotate(30deg)",
  },
});

const Widget = styled("div")(({ theme }) => ({
  padding: 16,
  borderRadius: 16,
  width: 343,
  maxWidth: "100%",
  margin: "auto",
  position: "relative",
  zIndex: 1,
  backgroundColor: "rgba(255,255,255,0.4)",
  backdropFilter: "blur(40px)",
  ...theme.applyStyles("dark", {
    backgroundColor: "rgba(0,0,0,0.6)",
  }),
}));

const CoverImage = styled("div")({
  width: 100,
  objectFit: "cover",
  overflow: "hidden",
  flexShrink: 0,
  borderRadius: 8,
  backgroundColor: "rgba(0,0,0,0.08)",
  "& > img": {
    width: "100%",
  },
});

const TinyText = styled(Typography)({
  fontSize: "0.75rem",
  opacity: 0.38,
  fontWeight: 500,
  letterSpacing: 0.2,
});

interface Song {
  id: number;
  category: number;
  image: string;
  substr: string;
  multi_files: boolean;
  nr_files: number;
  title: string;
  youtube: string;
}

interface Props {
  currentSong: Song;
  isPlaying: Boolean;
  onSongEnd: (id: number) => void;
  onSongNext: (id: number) => void;
  onSongPrev: (id: number) => void;
}

const MusicPlayerSlider: React.FC<Props> = ({
  currentSong,
  isPlaying,
  onSongEnd,
  onSongNext,
  onSongPrev,
}) => {
  const [duration, setDuration] = useState(0); // seconds
  const [durationTotal, setDurationTotal] = useState(0); // seconds
  const [endAudio, setEndAudio] = useState(false);
  const [currentTime, setCurrentTime] = useState(0);
  const [position, setPosition] = useState(0);
  const [sound, setSound] = useState(1);
  const [paused, setPaused] = useState(true);
  const [count, setCount] = useState(0);
  function formatDuration(value) {
    const minute = Math.floor(value / 60);
    const secondLeft = value - minute * 60;
    return `${minute}:${secondLeft < 10 ? `0${secondLeft}` : secondLeft}`;
  }
  /*const melodii = [
   {
    title: 'Adrian-Minune--Asa-Sunt-Zilele-Mele--Muzica-Originala',
    image: imagine,
    song: melodie
  }];*/
  //const [melodii,setMelodii] = useState([]);
  //console.log(melodii);
  const audioRef = useRef(null);
  const videoRef = useRef(null);
  const currentSongRef = useRef(currentSong);

  useEffect(() => {
    currentSongRef.current = currentSong;
  }, [currentSong]);

  const [category, setCategory] = useState([
    {
      id: 1,
      title: "Muzica Populara",
    },
    {
      id: 2,
      title: "Manele",
    },
    {
      id: 3,
      title: "Usoara",
    },
  ]);

  const [isVideo, setIsVideo] = useState(false);

  useEffect(() => {
    const video = videoRef.current;
    if (isPlaying) {
      //video.src = currentSong.video;
      //video.play();
      const captureStream = mediaElement =>!!mediaElement.mozCaptureStream ? mediaElement.mozCaptureStream() : mediaElement.captureStream();

    interface MediaFragment {
      src: string;
      from: string | null;
      to: string | null;
    }

    type MediaFragmentRecorderProps = {
      //urls: Array<{ src: string; from: string | null; to: string | null }>;
      //urls: [];
      urls: MediaFragment[];
      width?: number;  // Video width
      height?: number; // Video height
    };
  
    const MediaFragmentRecorder = async ({
      urls = [],
      width = 320,
      height = 240,
    }: MediaFragmentRecorderProps) => {
      if (urls.length === 0) {
        throw new TypeError("no urls passed to MediaFragmentRecorder");
      }
      const chunks = [];
      let duration = 0;
      //console.log(urls);
      let media = await Promise.all(
        urls.map(async ({ from, to, src }, index) => {
          //console.log(src);
          const url = new URL(src);
          //console.log(url);
          // get media fragment hash from `src`
          if (url.hash.length) {
            [from, to] = url.hash.match(/\d+|\d+\.|d+/g);
          }
          return {
            blob: await fetch(src).then((response) => response.blob()),
            from,
            to,
          };
        })
      );
      for (let { from, to, blob } of media) {
        await new Promise((resolve) => {
          let recorder;
          const blobURL = URL.createObjectURL(blob);
          //const video = document.createElement("video");
          const video = videoRef.current;
          const track = document.createElement("track");
          track.kind = "subtitles";
          track.label = "Romana";
          track.srclang = "ro";
          track.src = currentSong.substr;
          track.default = true;
          video.appendChild(track);
          video.autoplay = true;
          video.preload = "auto";
          //document.body.appendChild(video);
          video.addEventListener(
            "play",
            async (e) => {
              const mediaStream = captureStream(video);
              try {
                console.log(mediaStream.getVideoTracks()[0]);
                // variable video width, height should be recorded
                // https://bugs.chromium.org/p/chromium/issues/detail?id=972470
              } catch (e) {
                console.error(
                  e,
                  mediaStream.getVideoTracks()[0].getSettings(),
                  mediaStream.getVideoTracks()[0].getConstraints()
                );
              }
              recorder = new MediaRecorder(mediaStream, {
                mimeType: "video/webm;codecs=vp8,opus",
              });
              recorder.addEventListener(
                "dataavailable",
                async (e) => {
                  console.log(e);
                  const chunk = await new Response(e.data).arrayBuffer();
                  chunks.push(chunk);
                  console.log(chunks);
                  resolve(chunk);
                  URL.revokeObjectURL(blobURL);
                },
                { once: true }
              );
              recorder.start();
            },
            {
              once: true,
            }
          );
          video.addEventListener(
            "pause",
            (e) => {
              console.log(
                video.played.end(0) - video.played.start(0),
                video.currentTime - parseInt(from),
                video.currentTime
              );
              recorder.stop();
              duration += video.currentTime - parseInt(from);
              //video.remove();
            },
            {
              once: true,
            }
          );
          video.src = `${blobURL}#t=${from},${to}`;
        });
      }
      return {
        chunks,
        duration,
        width,
        height,
      };
    };
    
    const context_in_time_2011 = require.context(
      "../../assets/videos/in_time_2011/",
      false,
      /\.mp4$/
    );
    console.log(context_in_time_2011.keys().length);

    /*const urls_tmp = context_in_time_2011.keys().map(async (key) => {
      //console.log(window.location.href.split('//')[1].substring(0,3));
      let url = window.location.href.split('//')[1].substring(0,3)=='loc'?process.env.REACT_APP_URL_LOCAL:process.env.REACT_APP_URL;
      //console.log(url);
      const src = url+context_in_time_2011(key);
      //console.log(src);
      let from = "0";
      let to = "0";
    
      //const match = key.match(/(\d+)-(\d+)/);
      //if (match) {
      //  from = match[1];
      //  to = match[2];
      //}

      const video = document.createElement("video");
      video.src = src;
  
      const duration = await new Promise<number>((resolve) => {
        video.addEventListener("loadedmetadata", () => {
          resolve(video.duration); // Get the duration of the video
        });
  
        // Handle errors or invalid metadata
        video.addEventListener("error", () => {
          resolve(0); // Default to 0 if the video fails to load
        });
      });

      to = Number.isFinite(duration) ? duration.toString() : "0";

      return { src, from, to };
    });*/

      //let urls_tmp: MediaFragment[] = [];
      let currentFrom = 0; 
      //let count = 0
      console.log(currentSong.nr_files);
      const keys = context_in_time_2011.keys().slice(0, currentSong.nr_files);
      let totalDuration = 0;
      Promise.all(
        keys.map(async (key) => {
          let url =
            window.location.href.split("//")[1].substring(0, 3) === "loc"
              ? process.env.REACT_APP_URL_LOCAL
              : process.env.REACT_APP_URL;
      
          const src = url + context_in_time_2011(key);
          console.log(src);

          const video = document.createElement("video");
          video.src = src;

          const duration = await new Promise<number>((resolve) => {
            video.addEventListener("loadedmetadata", () => {
              totalDuration += video.duration;
              resolve(video.duration); 
            });
      
            video.addEventListener("error", () => {
              resolve(0);
            });
          });
          //console.log(duration);
          // Ensure `from` and `to` are strings
          /*let from_tmp = 0;
          from_tmp = parseInt(key)===0?0:from_tmp+duration;
          let from = from_tmp.toString();
          let to = Number.isFinite(duration) ? duration.toString() : "0";*/
          const from = currentFrom.toString();
          const to = duration.toString();

          return { src, from, to };
        })
      ).then((result) => {
        // Assign the resolved data to `urls_tmp`
        //urls_tmp = result;
      //console.log(urls_tmp);
      //const urls = context_in_time_2011.keys().map((key) => { return key;});
      //const urls = urls_tmp.slice(0, Math.ceil(urls_tmp.length / 2));
      const urls = result;
      console.log(urls);

        MediaFragmentRecorder({
          urls,
        })
        .then(({ chunks, duration, width, height }) => {
          let recorder, mediaStream;
          // variable video width, height should be displayed
          // https://bugs.chromium.org/p/chromium/issues/detail?id=983777
          //const video = document.createElement("video");
          const video = videoRef.current;
          /*const track = document.createElement("track");
          track.kind = "subtitles";
          track.label = "Romana";
          track.srclang = "ro";
          track.src = currentSong.substr;
          track.default = true;
          video.appendChild(track);
          video.controls = true;
          video.autoplay = true;
          document.body.appendChild(video);*/
          console.log(chunks, duration);
          const mediaSource = new MediaSource();
          let sourceBuffer;
          const mimeCodec = "video/webm;codecs=vp8,opus";
          const sourceOpen = (e) => {
            console.log(e);
            sourceBuffer = mediaSource.addSourceBuffer(mimeCodec);
            sourceBuffer.addEventListener("updateend", (e) => {
              console.log("sourceOpen updateend",e, video.currentTime, mediaSource);
            });
            sourceBuffer.appendBuffer(chunks.shift());
          };
          const handleWaiting = (e) => {
            console.log(e, video.currentTime, recorder && recorder.state);
            sourceBuffer.abort();
            sourceBuffer.timestampOffset = video.currentTime;
            if (chunks.length) {
              sourceBuffer.appendBuffer(chunks.shift());
            } else {
              try {
                if (recorder.state === "recording") {
                  recorder.stop();
                  video.removeEventListener("waiting", handleWaiting);
                  mediaSource.endOfStream();
                  video.load();
                  //video.remove();
                  console.log(video.currentTime, duration, mediaSource.duration);
                }
              } catch (e) {
                console.error(e);
                console.trace();
              }
            }
          };
          mediaSource.sourceBuffers.addEventListener("addsourcebuffer", (e) =>
            console.log(e)
          );
          video.addEventListener("canplay", (e) => {
            console.log("canplay",
              e,
              video.readyState,
              video.paused,
              video.duration,
              mediaSource.duration
            );
          });
          video.addEventListener("playing", (e) => {
            console.log("playing",e);
          });
          video.addEventListener(
            "play",
            (e) => {
              mediaStream = captureStream(video);
              console.log("play mediaStream: ",mediaStream, mediaStream.getTracks());
              mediaStream.addEventListener("inactive", (e) => console.log(e));
              recorder = new MediaRecorder(mediaStream, {
                mimeType: "video/webm;codecs=vp8,opus",
              });
              recorder.addEventListener("dataavailable", (e) => {
                //let media = document.createElement("video");
                let media = videoRef.current;
                media.controls = true;
                media.addEventListener("loadedmetadata", (e) => {
                  console.log("play loadedmetadata",
                    e,
                    e.target,
                    duration,
                    mediaSource.duration
                  );
                });

                document.body.appendChild(media);
                media.src = URL.createObjectURL(e.data);
              });
              recorder.addEventListener("stop", (e) => console.log("stop",e));
              recorder.start();
              console.log("recorder start: ",e, duration, video.duration, mediaSource.duration);
            },
            {
              once: true,
            }
          );
          video.addEventListener("waiting", handleWaiting);
          video.addEventListener("pause", (e) =>
            console.log(e, duration, video.duration, mediaSource.duration)
          );
          video.addEventListener("stalled", (e) => console.log(e));
          video.addEventListener("loadedmetadata", (e) => console.log(e));
          video.addEventListener("loadeddata", (e) => console.log(e));
          video.addEventListener("seeking", (e) => console.log(e));
          video.addEventListener("seeked", (e) => console.log(e));
          video.addEventListener("durationchange", (e) => console.log(e));
          video.addEventListener("abort", (e) => console.log(e));
          video.addEventListener("emptied", (e) => console.log(e));
          video.addEventListener("suspend", (e) => console.log(e));
          mediaSource.addEventListener("sourceopen", sourceOpen, {
            once: true,
          });
          video.src = URL.createObjectURL(mediaSource);
        })
        .catch((e) => console.error(e));
      });
    }
  }, [isPlaying, currentSong, onSongEnd]);

  const handleVideoEnd = async () => {
    //await onSongEnd();

    /*setTimeout(() => {
      //audioRef.current.pause();
      setPaused(true);
      setIsVideo(true);
      console.log("handleVideoEnd: " + JSON.stringify(currentSongRef.current));
      if (isVideo) {
        videoRef.current.src = currentSongRef.current.video;
      } else {
        videoRef.current.src = null;
      }
    }, 300);*/
  };

  const capitalizeFirstLetter = (string) => {
    //string = string.toLowerCase();
    //return string.replace(/^./, string[0].toUpperCase());
    return string
      .toLowerCase()
      .split(" ")
      .map((word) => word.charAt(0).toUpperCase() + word.slice(1))
      .join(" ");
  };


  return (
    <Box
      sx={{
        width: "90%",
        overflow: "hidden",
        position: "relative",
        p: 3,
        margin: "0 auto",
      }}
    >
      <Widget>
        <Box sx={{ display: "flex", alignItems: "center" }}>
          <CoverImage>
            <img
              alt={currentSong.title}
              src={currentSong.image}
              style={{ display: "cover" }}
            />
          </CoverImage>
          <Box sx={{ ml: 1.5, minWidth: 0 }}>
            <Typography
              variant="caption"
              sx={{ color: "text.secondary", fontWeight: 500 }}
            >
              Duration - {formatDuration(durationTotal)}
            </Typography>
            <Typography noWrap>
              <b>{category[currentSong.category - 1].title}</b>
            </Typography>
            <Typography noWrap sx={{ letterSpacing: -0.25 }}>
              {capitalizeFirstLetter(currentSong.title)}
            </Typography>
          </Box>
        </Box>
        <video
          id="video"
          width={320}
          ref={videoRef}
          controls
          autoPlay={true}
          style={{ margin: "0 auto" }}
        />
      </Widget>
      <WallPaper />
    </Box>
  );
};

export default MusicPlayerSlider;
